@font-face {
    font-family: "mainFont";
    src: url('/src/assets/fonts/Cormorant_Garamond/CormorantGaramond-Light.ttf');
}
html,
body {
    height: 100%;
    font-family: mainFont;
}
.App {
    background: #FFF;
}

button {
    display: flex;
    padding: 1vw 1vw;
    justify-content: center;
    align-items: center;
    gap: 0.5vw;
    flex-shrink: 0;
    border-radius: 1.5vw;
    border: 0.3vw solid #78424A;
    background: none;
    font-family:'Cormorant Garamond';
    cursor: pointer;
    color: var(--black, #393939);
    font-size: 2vw;
    font-weight: 400;
    line-height: 2vw;
    box-sizing: border-box;
    text-transform: uppercase;
}
button:hover {
    background-color: #78424A;
    color: #fff;
    display: flex;
    flex-direction: row;
    transition-duration: 0.5s;
}
button:hover .material-symbols-outlined{
    color:#fff;
    transition-duration: 0.5s;
}
.white-button,
.white-text {
    color: #fff;
}
.white-arrow {
    color: #fff !important;
    font-size: 2vw;
}
hr {
    height: 0.3vw;
    margin: 0 3%;
    border: none;
    background-color: #5E2129;
}
a {
    color: var(--black, #393939);
    font-size: 1.7vw;
    font-weight: 400;
    line-height: 1.7vw;
}
.material-symbols-outlined {
    color:#393939; 
    cursor: pointer;
    font-size: 2vw;
}
.inline-input {
    padding: 1vw 1vw;
    border-radius: 1.5vw 0 0 1.5vw;
    flex-shrink: 0;
    border: 0.3vw solid #78424A;
    color: var(--black, #393939);
    font-family: Cormorant Garamond;
    font-size: 2vw;
    font-weight: 400;
    line-height: 2vw;
    box-sizing: border-box;
    width: 70%;
}
.inline-input-submit-btn {
    left: -5px;
    display: flex;
    padding: 1vw 1vw;
    justify-content: center;
    align-items: center;
    gap: 1vw;
    flex-shrink: 0;
    border-radius: 0 1.5vw 1.5vw 0;
    border: 0.3vw solid #78424A;
    background: none;
    font-family:'Cormorant Garamond';
    cursor: pointer;
    color: var(--black, #393939);
    font-size: 2vw;
    font-weight: 400;
    line-height: 2vw;
    box-sizing: border-box;
    width: 30%;
}
.inline-input-submit-btn:hover {
    border: 3px solid #78424A;
    background-color: #78424A;
    color: white;
    transition-duration: 0.5s;
}
.close-btn{
    position: absolute;
    top: 2vw;
    right: 2vw;
    font-size: 2vw;
}


/*header advert*/
.main-page-advert-container {
    flex-shrink: 0;
    background-image: linear-gradient(90deg, #78424A 0, #78424A 50%, #592129 50%, #592129 100%);
    background-size: 200%;
    background-position-x: 100%;
    transition: background .3s ease-out;
    padding: 1vw 4% 1vw 9%;
    display: flex;
    flex-direction: row;
    align-items: center;
    box-sizing: border-box;

    .main-page-advert-description{
        color: var(--white, #FFF);
        font-size: 2vw;
        font-weight: 400;
        line-height: 80%;
        margin: 0;
        cursor: default;
        text-transform: uppercase;
    }
    .main-page-advert-description::before{
        position: absolute;
        left: 6%;
        font-family: "Material Symbols Outlined";
        content: '\ead5';
        font-size: 2vw;
    }

    .main-page-advert-button {
        border: none;
        color: var(--white, #FFF);
        margin-left: auto;
        padding: 0% !important;
    }
    .main-page-advert-button:hover{
        border: none;
        background: none;
    }
}
.main-page-advert-container:hover{
    background-position-x: 0;
}

/*header*/
header {
    position: relative;
    box-sizing: border-box;
    height: 9vw;
    padding: 2vw 4% 2vw 6%;
    flex-shrink: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;

    .main-page-logo {
        cursor: pointer;
        position: absolute;
        width: 20%;
        margin-left: 50%;
        transform: translate(-90%, 0%);
    }

    .menu-icon{
        display: none;
        font-size: 3vw;
    }

    .main-page-header-navigation {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        box-sizing: border-box;
        width: 25%;

        .main-page-header-navigation-dropdown {
            position: relative;
            display: inline-block;

            .dropdown-content {
                position: absolute;
                background-color: #ffffff;
                min-width: 160px;
                overflow: auto;
                z-index: 1;
                a {
                    color: #393939;
                    padding: 1vw;
                    text-decoration: none;
                    display: block;
                }
                a:hover {
                    background-color: rgba(118, 65, 73, 0.25);
                }
            }
        }
        .main-page-header-navigation-option {
            display: flex;
            align-items: center;
            font-size: 2vw;
            position: relative;
            line-height: 2vw;
            text-decoration: none;
            margin: 0;
            text-transform: uppercase;
            cursor: pointer;
        }
        .main-page-header-navigation-option::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 0.1em;
            background-color: #393939;
            opacity: 0;
            transition: opacity 300ms, transform 300ms;
        }
        .main-page-header-navigation-option:hover::after,
        .main-page-header-navigation-option:focus::after {
            opacity: 1;
            transform: translate3d(0, 0.2em, 0);
        }
        .main-page-header-navigation-option::after {
            opacity: 1;
            transform: scale(0);
            transform-origin: center;
        }
        .main-page-header-navigation-option:hover::after,
        .main-page-header-navigation-option:focus::after {
            transform: scale(1);
        }
    }
    .main-page-header-icons {  
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
        gap: 3vw;

        .header-icon {
            font-size: 3vw;
            cursor: pointer;
        }
    }
}

#header-search-bar-container {
    display: none;
    width: 90%;
    margin: auto;
    padding: 3vw 0 0;
}

/*promotion*/
.main-page-promotion-gallery-container{
    position: relative;
    box-sizing: border-box;
    margin: 4% 0%;
    padding: 0% 3%;
    height: 100%;

    .promotion-content {
        position: relative;

        .promotion-img {
            width: 100%;
            height: 100%;
        }
        .promotion-heading {
            position: absolute;
            top: 9%;
            right: 3%;
            font-weight: 400;
            font-size: 3.2vw;
        }
        .promotion-heading-second {
            right: 12%;
        }
        .promotion-text {
            position: absolute;
            width: 50%;
            top: 32%;
            right: 4%;
            font-size: 2vw;
            line-height: 2vw;
        }
        .promotion-text-second {
            right: 15%;
            width: 60%;
        }
        .main-page-promotion-button{
            position: absolute;
            top: 70%;
            right: 10%;
        }
    }
}
.main-page-promotion-mobile-gallery-container {
    display: none;
} 
.slick-dots{
    bottom: 1vw !important;
}

/* themed products block */
.main-page-themed-products-block-container {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding: 0 4% 8% 4%;

    .themed-products-heading {
        margin: 0;
        color: var(--black, #393939);
        font-size: 4vw;
        font-weight: 600;
        line-height: normal;
        text-align: center;
    }
    .products-container {
        margin-top: 3%;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 6vw;

        .product {
            position: relative;
            display: flex;
            flex-direction: column;
            box-sizing: border-box;

            .product-add-to-cart-button {
                margin: 0;
                width: 100%;
                padding: 0.5vw 1vw;
                position: absolute;
                border: none;
                border-radius: 0;
                background: #78424A;
                color: var(--white, #FFF);
                font-size: 1.5vw;
                gap: 1vw;  
                opacity: 50%;
                display: none;

                .product-add-to-cart-button-input-label {
                    margin: 0;
                    height: 100%; 
                    cursor: pointer;
                }
            }
            .product-add-to-cart-button:hover {
                opacity: 100%;
            }
            img {
                margin: 0;
                width: 100%; 
            }
            .product-info {
                box-sizing: content-box;
                height: 11vw;
                display: flex;
                flex-direction: column;
                justify-content: space-between;

                .product-description {
                    margin: 2vw 0 0 0;
                    text-align: center;
                    color: var(--black, #393939);
                    font-size: 2vw;
                    font-weight: 400;
                    line-height: 2vw;
                    cursor: pointer;
                }
                .product-price {
                    text-align: center;
                    margin: 0px;
                    color: var(--black, #393939);
                    font-size: 3vw;
                    font-weight: 700;
                    line-height: 2.5vw;
                    cursor: pointer;
                }
            }
        }
        .product:hover .product-add-to-cart-button {
            display: flex;
            bottom: 11vw;
        }
        .products-additional-block {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        
            .products-additional-block-text {
                color: var(--black, #393939);
                text-align: center;
                font-size: 2vw;
                font-weight: 400;
                line-height: 2vw;
                margin: 0 0 3vw 0;
            }
            .products-additional-block-button {
                font-size: 2vw;
                line-height: 2vw;
            }
        }
    }
}
