/*categories block*/
.main-page-categories-block-container {
    box-sizing: border-box;
    text-align: center;
    padding: 4vw 6%;
    flex-shrink: 0;
    background: rgba(120, 66, 74, 0.50);

    .main-page-categories-block-heading {
        color: var(--black, #393939);
        margin: 0px;
        font-size: 5vw;
        font-weight: 600;
        line-height: 5vw;
    }
    .main-page-categories-block-items {
        text-align: center;
        margin-top: 4vw;
        display: flex;
        justify-content: space-between;

        .category-item {
            box-sizing: border-box;
            display: flex;
            width: 15vw;
            flex-direction: column;

            .main-page-categories-item {
                width: 100%;
            }
            .category-item-name {
                text-align: center;
                font-size: 2vw;
                font-weight: 400;
                line-height: 2vw;
                background: #5E2129;
                box-sizing: border-box;
                padding: 1vw 1vw;
                margin: 0;
                cursor: pointer;
                text-transform: uppercase;
            }
            .category-item-name:hover {
                background-color: #78424A;
            }
        }
    }
}

/*quiz-block*/
.main-page-quiz-block-container {
    box-sizing: border-box;
    position: relative;
    margin: 6% 0;
    padding: 0 3%;

    .main-page-quiz-img {
        width: 100%; 
        height: auto;
    }
    .main-page-quiz-mobile-img {
        display: none;
    }
    .quiz-block-button {
        position: absolute;
        top: 70%;
        left: 64%;
        padding: 2vw 1vw;
    }
}

/*footer*/
footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 5% 6%;
    box-sizing: border-box;

    .footer-categories-block {
        width: 60%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        box-sizing: border-box;

        .footer-category-container {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            box-sizing: border-box;
            width: 20vw;

            .footer-category-name {
                margin: 0 0 4vw 0;
                padding: 1.5vw 2.5vw;
                border: 0.3vw solid #78424A;
                display: flex;
                flex-direction: column;
                justify-content: center;
                flex-shrink: 0;
                color: var(--black, #393939);
                text-align: center;
                font-size: 2vw;
                font-weight: 400;
                line-height: 2vw;
                text-transform: uppercase;
            }
            .footer-navigation {
                display: flex;
                flex-direction: column;
                gap: 2vw;
            
                a {
                    text-transform: uppercase;
                }
            }
        }
    }
    .footer-authentication-n-contacts-container {
        width: 40%;
        margin-left: auto;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        box-sizing: border-box;
    
        .footer-authentication-heading {
            margin: 0;
            color: var(--main, #78424A);
            font-size: 4vw;
            font-weight: 700;
            line-height: normal;
            text-transform: uppercase;
        }
        .footer-authentication-info {
            margin: 2vw 0 2vw 0; 
            color: var(--black, #393939);
            font-size: 2.5vw;
            font-weight: 400;
            line-height: 2.5vw;
        }
        .footer-form {
            display: flex;
            flex-direction: row;
            box-sizing: border-box;
            width: 100%;
        
            .footer-sign-up-input {
                height: 5.5vw;
            }
            .footer-sign-up-submit-btn {
                height: 5.5vw;
            }
        }
        .footer-contacts {
            margin: 0px;
            color: var(--black, #393939);
            font-size: 2.5vw;
            font-weight: 400;
            line-height: 2.5vw;
            margin: 3vw 0;
            text-transform: uppercase;
        }
        .footer-share-icons {
            display: flex;
            gap: 3vw;
            height: 3vw;
        
            .fa-brands {
                color: #393939; 
                cursor: pointer;
                line-height: 1 !important;
                font-size: 3vw;
            }
        }
    }
}
.scroll-up-button { 
    position: relative;
    color: #393939;
    font-weight: 400;
    font-size: 2vw;
    line-height: 2vw;
    padding: 1vw 2vw;
    box-sizing: inherit;
    text-transform: uppercase;
    cursor: pointer;
    float: right;
    margin-right: 3%;
    text-decoration: none;
}
.scroll-up-button::after{
    color: #393939;
    position: absolute;
    right: 0;
    font-family: "Material Symbols Outlined";
    content: '\e5d8';
}

.sign-in-modal{
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.4);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;

    .sign-in-modal-body{
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 50%;
        transform: translate(50%, -50%);
        background-color: #fff;
        padding: 5vw;
        border: none;
        align-items: center;
        justify-content: center;
        box-sizing: border-box;

        .sign-in-modal-heading {
            font-size: 2.5vw;
            margin:0;
            color: #592129;
            text-transform: uppercase;
        }
        .sign-in-modal-form {
            width: 100%;
            gap: 2vw;
            .sign-in-modal-form-input {
                margin: 2vw 0;
                display: flex;
                flex-direction: column;

                .sign-in-modal-form-input-label{
                    font-size: 2vw;
                }
                .sign-in-modal-form-input-line {
                    padding: 0.8vw;
                }
            }
            
        
        }
        .sign-in-modal-form-button {
            width: 40%;
            margin: 2vw auto;
            background-color: #78424A;
            color: #fff;
        }
        a{
            margin: 1vw 0 3vw;
            font-size: 2vw;
        }
        p{
            margin: 0;
            font-size: 2vw;
        }
    }
}
/* menu overlay */
#sidebar-menu {
    display: block;
    height: 100%;
    width: 100%;
    top: 0;
    left:0;
    position: fixed;
    z-index: 1;
    transition: 2s;
    background-color: rgba(0, 0, 0, 0.7);

    .sidebar-menu-content {
        top:0;
        height: 100%;
        position: relative;
        width: 60%;
        background-color: #fff;
        padding: 2vw 0;
        h1{
            text-align: center;
            font-size: 3vw;
            color: #592129;
            text-transform: uppercase;
        }
        .sidebar-menu-categories {
            margin: 0;
            padding: 0;
            box-sizing: border-box;

            .menu-category{
                margin: 0;
                list-style: none;
                position: relative;
                display: block;
                color: #592129;
                text-align: left;
                font-size: 2vw;
                font-weight: 400;
                line-height: 2vw;
                position: relative;
                cursor: pointer;
                text-transform: uppercase;
                padding: 1.5vw 3% 1.5vw 4vw;
            }
            .menu-category::after{
                position: absolute;
                right: 3%;
                font-family: "Material Symbols Outlined";
                content: '\e5e1';
            }
            .menu-category-club::after{
                content: "" !important;
            }
            .menu-category-my-account::after{
                content: "\e853"!important;
            }
        }
        .menu-support{
            margin: 2vw 0 3vw 0;
        
            a {
                display: block;
                padding: 1vw 4vw;
                text-decoration: none;
                font-size: 1.5vw;
                color: #393939;
                text-transform: uppercase;
            }
        }
        .menu-share-icons{
            display: flex;
            gap: 2vw;
            padding: 0 4vw;
            
            i {
                font-size: 2.5vw;
                line-height: 2.5vw;
                color:#393939;
            }
        }
    }
}

@media screen and (max-width: 500px) { /* mobile verion*/
    .products-additional-block{
        display: none;
    }
    footer{
        flex-direction: column;

        .footer-categories-block{
            margin-bottom: 6%;
            width: 100%;

            .footer-category-container{
                width: 25vw;

                .footer-category-name {
                    font-size: 3vw;
                }
                .footer-navigation a {
                    font-size: 3vw;
                    line-height: 3vw;
                }
            }
        }
        .footer-authentication-n-contacts-container{
            width: 100%;

            .footer-authentication-block-container .footer-form .footer-sign-up-input{
                width: 80%;
            }
            .footer-authentication-block-container .footer-form .footer-sign-up-submit-btn{
                width: 20%;
            }
        }
    }
    
    .main-page-promotion-gallery-container{
        display: none;
    }

