            padding: 3vw 2vw;
            font-size: 3.5vw;
        }
        .promotion-content .white-button{
            position: absolute;
            top: 68%;
            right: 43%;
            padding: 3vw 2vw;
            font-size: 3.5vw;
        }
        .promotion-content .promotion-img{
            width: 100%;
        }
        .promotion-content .promotion-heading {
            position: absolute;
            top: 7%;
            right: 60.5%;
            font-size: 5vw;
            font-weight: 400;
        }
        .promotion-content .promotion-heading-second {
            right: 42%;
            font-weight: 400;
        }
        .promotion-content .promotion-text {
