            width: 9%;
            top: 28%;
            right: 60.1%;
            font-size: 3.2vw;
            line-height: 3.5vw;
        }
        .promotion-content .promotion-text-second {
            right: 40.5%;
            width: 12%;
            top: 30%;
        }
    }
    .slick-dots{
        bottom: 2vw !important;
    }
    /*quiz*/
    .main-page-quiz-img {
        display: none;
    }
    .main-page-quiz-block-container .main-page-quiz-mobile-img {
        display: block;
        width: 100%; 
        height: auto;
    }
    .main-page-quiz-block-container .quiz-block-button {
        top: 88.5%;
        left: 50%;    
        padding: 3vw 2vw;
        transform: translate(-50%, 0%);
        font-size: 3.5vw;
    }
    .material-symbols-outlined {
        font-size: 3.5vw;
    }

    /*menu*/
    #sidebar-menu .sidebar-menu-content {
        display: flex;
        flex-direction: column;
        width: 100%;
        justify-content: center;

        .close-btn{
            top: 5vw;
            right: 5vw;
            font-size: 5vw;
        }
        h1{
            font-size: 5vw;
            margin: 4vw 0;
        }
        .sidebar-menu-categories {
            .menu-category{
                font-size: 4vw;
                line-height: 4vw;
                padding: 3vw 3% 3vw 4vw;
            }
            .menu-category::after{
                position: absolute;
                right: 3%;
                font-family: "Material Symbols Outlined";
                content: '\e5e1';
            }
            .menu-category-club::after{
                content: "" !important;
            }
            .menu-category-my-account::after{
                content: "\e853"!important;
            }
        }
        .menu-support{
            margin: 6vw 0 8vw 0;
            a {
                padding: 2.5vw 4vw;
                font-size: 3vw;
            }
        }
        .menu-share-icons{
            gap: 4vw;
            i {
                font-size: 5vw;
                line-height: 5vw;
            }
        }
    }

    .sign-in-modal .sign-in-modal-body{
        width: 100%;
        transform: translate(0, -50%);

        .sign-in-modal-heading {
            font-size: 4.5vw;
        }
        .sign-in-modal-form .sign-in-modal-form-input .sign-in-modal-form-input-label{
            font-size: 4vw;
        }
        .sign-in-modal-form .sign-in-modal-form-input .sign-in-modal-form-input-line{
            padding: 2vw;
        }
        .sign-in-modal-form-button{
            font-size: 3vw;
            padding: 2vw;
            margin:3vw;
        }
        a, p{
            font-size: 4vw;
        }
    }
        
}

@media screen and (max-width: 650px) { /*plate version*/
    .main-page-themed-products-block-container .themed-products-heading {
        font-size: 7vw;
    }
    .main-page-themed-products-block-container .products-container {
        grid-template-rows: repeat(3, 1fr);
        grid-template-columns: 1fr;
        grid-gap: 5vw;
        justify-content: center;
        margin-top: 6vw;

        .product{
            display: grid;
            grid-template-columns: 35% 60%;
            margin: 0 auto;
            justify-content: space-between;

            .product-add-to-cart-button{
                display: flex;
                bottom: 0;
                right: 0;
                width: 60%;
                gap: 2vw;
                padding: 1vw;
                opacity: 100%;
            }
            .product:hover .product-add-to-cart-button{
                bottom: 0;
                right: 0;
            }
            .product-info {
                height: 70%;
                justify-content: space-between;

                .product-description{
                    margin-top: 4vw;
                    font-size: 3.5vw;
                    line-height: 4vw;
                }
                .product-price {
                    font-size: 5vw;
                }
            }  
        }
    }
    
    .main-page-themed-products-block-container .products-container .products-additional-block{
        display: none;
    }
    
    .scroll-up-button { 
        font-size: 3vw;
        line-height: 3vw;
        padding: 3vw;
    }
    .scroll-up-button::after {
        font-size: 3vw;
    }
}

@media (max-width: 1200px) { /* plate version*/
    .main-page-advert-container {
        display: none;
    }
    header .main-page-header-navigation {
        display: none;
    }
    header .main-page-header-navigation.active {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 70px;
        right: 0;
        background-color: #393939;
    }
    header .menu-icon {
        display: block;
        cursor: pointer;
    }
    header .main-page-header-icons .header-my-account{
        display: none;
    }
}

